<!-- 404 -->
<template>
  <button class="btn btn-outline-secondary btn-sm m-3" type="button" @click="$router.go(-1)">返回上一頁</button>
  <div class="errorPage">
    <div class="errors">404</div>
    <span class="infos">File not found</span>
    <div class="wrap">
      <div id="clock" v-text="clock"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NotFound",
  data() {
    return {
      clock: "",
    };
  },
  methods: {
    time() {
      const now = new Date();
      this.clock = now.toLocaleTimeString("it-IT") + "." + now.getMilliseconds();
    },
    init() {
      window.setInterval(this.time, 66);
    },
  },
  mounted: function () {
    this.init();
  },
};
</script>

